@font-face {
  font-family: 'Miltonian-Regular';
  src: local('Miltonian-Regular'), url(./fonts/Miltonian-Regular.ttf) format('truetype');
}
@font-face {
  font-family: 'OpenSans-Bold';
  src: local('OpenSans-Bold'), url(./fonts/OpenSans-Bold.ttf) format('truetype');
}
@font-face {
  font-family: 'OpenSans-Light';
  src: local('OpenSans-Light'), url(./fonts/OpenSans-Light.ttf) format('truetype');
}
@font-face {
  font-family: 'OpenSans-Regular';
  src: local('OpenSans-Regular'), url(./fonts/OpenSans-Regular.ttf) format('truetype');
}
@font-face {
  font-family: 'OpenSans-SemiBold';
  src: local('OpenSans-SemiBold'), url(./fonts/OpenSans-SemiBold.ttf) format('truetype');
}
body {
  margin: 0;
  font-family: 'Miltonian-Regular';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'OpenSans-Regular', Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.logoFont {
  font-family:'Miltonian-Regular';
}
.bgBlack {
  background-color: black;
}
.brownColor {
  color: #603913;
}
.brownBackgroundColor {
  background-color: #603913 !important;
}
.backGrey {
  background-color: #f3f3f3;
}
.opacity50 {
  opacity: 10%;
}
@media only screen and (max-width: 600px) {
  .topText {
    font-size: 13px;
  } 
}
.bannerImageWidth{
  width: 60%
}

 .black{
  color: #000 !important;
  font-weight:bold !important;
}
.hoverred:hover{
  color:#603913 !important;
  transition: 0.5s !important;
  text-decoration:none !important;
}
.carousel .carousel-item{
  height: '50%';
}

.openSansRegular {
  font-family: 'OpenSans-Regular';
}
.openSansLight {
  font-family: 'OpenSans-Light';
}
.openSansBold {
  font-family: 'OpenSans-Bold';
}
.openSansSemiBold {
  font-family: 'OpenSans-SemiBold';
}
.rotate{
  animation: rotation 5s infinite linear;
}
@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
.carousel-control-prev-icon,
.carousel-control-next-icon {
  color: #603913 !important;
}
.btnIcons :hover {
  color: #fff;
}
.whiteColor{
  color: #fff;
}
.brownBorder{
  border-top: 2px solid #603913 ;
  width: 80px;
}
